import { Component, OnInit, OnDestroy } from '@angular/core';
import { Subscription, Subject } from 'rxjs';
import { Restaurant, Franchise, ResponseRestaurant } from '@models';
import { version } from '../../../../package.json';
import { FormBuilder } from '@angular/forms';
import { RestaurantService, FranchiseService } from '@services';
import { map } from 'rxjs/operators';
import { IRestaurant } from '@interfaces';
import { WorkingHour } from '@models';
import { NewRequestAlertService } from 'src/app/shared/services/request-alert.service';
import { Router } from '@angular/router';

@Component({
  selector: 'app-staff-layout',
  templateUrl: './staff-layout.component.html',
  styleUrls: ['./staff-layout.component.scss'],
})
export class StaffLayoutComponent implements OnInit, OnDestroy {
  /**
   * Routes to show at the lateral menu.
   */
  public routes: Array<{
    name: string;
    icon: string;
    route: string;
    notification?: boolean;
  }> = [
    {
      name: 'Solicitudes nuevas',
      icon: 'icon-requests',
      route: 'new-requests',
    },
    {
      name: 'Reservaciones',
      icon: 'icon-reservations',
      route: 'reservations',
    },
    {
      name: 'En servicio',
      icon: 'icon-active-orders',
      route: 'in-service',
    },
    {
      name: 'Cerradas',
      icon: 'icon-closed-orders',
      route: 'closed',
    },
    {
      name: 'Men',
      icon: 'icon-testimonial',
      route: 'menu',
    },
  ];

  /**
   * Working hour that is used to show the schedule at the lateral menu.
   */
  public todayWorkingHour: WorkingHour;

  /**
   * List to fill the restaurants select at the lateral menu.
   */
  public restaurants: Array<Restaurant>;

  /**
   * Current restaurant details.
   */
  private restaurant: Restaurant;

  /**
   * Form to manage the info at the select in the lateral menu.
   */
  public restaurantSelection = this.fb.group({
    restaurant: [null],
  });

  /**
   * Version displayed at the lateral menu.
   */
  public version: string = version;

  /**
   * Variable to store the subscription that give us the info of the selected restaurant.
   */
  private restaurantSubscription = Subscription.EMPTY;

  /**
   * Variable to store the subscription that give us the info of the selected franchise.
   */
  private franchiseSubscription = Subscription.EMPTY;

  /**
   * Variable to store the subscription that give us the info of the restaurants.
   */
  private restaurantsSubscription = Subscription.EMPTY;

  /**
   * Variable to store the subscription of wether there's a new Reservation or not.
   */
  private newRequestSubscription = Subscription.EMPTY;

  /**
   * Variable to store the subscription of wether there's a new Reservation or not.
   */
  private newInServiceRequestSubscription = Subscription.EMPTY;

  /**
   * Boolean that indicates if there is a new unread reservation request at the New Reservetions section.
   */
  public hasNewRequest: boolean;

  /**
   * Boolean that indicates if there is a new unread reservation request at the In Service section.
   */
  public hasNewInServiceReservations: boolean;

  public newNotificationSound =  new Subject();

  public upcomingNotificationSoundEvents =  this.newNotificationSound.asObservable();

  constructor(
    private fb: FormBuilder,
    private restaurantService: RestaurantService,
    private franchiseService: FranchiseService,
    private newRequestAlertService: NewRequestAlertService,
    public router: Router
  ) {}

  /**
   * When the component loads we get the info of the selected restaurant.
   */
  ngOnInit(): void {
    this.initRestaurantListener();
  }

  /**
   * When the component is destroyed we unsubscribe all posible subscriptions.
   */
  ngOnDestroy(): void {
    this.restaurantSubscription.unsubscribe();
    this.franchiseSubscription.unsubscribe();
    this.restaurantsSubscription.unsubscribe();
    this.newRequestSubscription.unsubscribe();
    this.newInServiceRequestSubscription.unsubscribe();
  }

  /**
   * Connects websocket and listens for new requests to display badge on side menu
   * @param restaurantId number containing restaurant id for socket
   */
  private getNewRequestStatus(restaurantId: number): void {
    this.newRequestAlertService.connect(restaurantId.toString());

    /**
     * It listens if there is a new reservation at the New Reservations section.
     */
    this.newRequestSubscription =
      this.newRequestAlertService.hasNewRequest.subscribe(
        (newRequest: boolean) => {
          if (newRequest) {
            this.newNotificationSound.next();
            if (!this.isThisTheNewReservationsCRUD) {
              this.hasNewRequest = newRequest;
            }
          } else {
            this.hasNewRequest = newRequest;
          }
        }
      );

    /**
     * It listens if there is a new reservation at the In Service section.
     */
    this.newInServiceRequestSubscription =
      this.newRequestAlertService.hasNewInServiceReservation.subscribe(
        (newRequest: boolean) => {
          if (!this.isThisTheInServiceReservationsCRUD) {
            this.hasNewInServiceReservations = newRequest;
          }
        }
      );
  }

  /**
   * The info of the selected restaurant is used to set the value of the restaurants select.
   * We get the restaurants from the server to fill the restaurants select.
   */
  private initRestaurantListener(): void {
    this.restaurantSubscription =
      this.restaurantService.currentRestaurant.subscribe(
        (restaurant: Restaurant) => {
          this.restaurant = restaurant;
          this.getNewRequestStatus(this.restaurant.id);
          this.setScheduleToShow(this.restaurant);
          this.restaurantSelection.get('restaurant').setValue(restaurant.id);
          this.franchiseSubscription =
            this.franchiseService.currentFranchise.subscribe(
              (franchise: Franchise) => {
                this.restaurantsSubscription = this.restaurantService
                  .getRestaurants(franchise.id)
                  .pipe(
                    map((response: ResponseRestaurant) => {
                      const restaurants = response.restaurants.map(
                        (pRestaurant: IRestaurant) => {
                          return new Restaurant(pRestaurant);
                        }
                      );
                      return restaurants;
                    })
                  )
                  .subscribe(() => {
                    this.restaurants = franchise.restaurants;
                  });
              }
            );
        }
      );
  }

  /**
   * If the selected restaurant is changed we set it as the selected one at the localstorage and reload the page.
   */
  public restaurantChanged(restaurantId: number): void {
    localStorage.setItem('restaurantId', JSON.stringify(restaurantId));
    location.reload();
  }

  /**
   * This function get the today's week day and use it to look the working hour that gets the today's schedule.
   * @param restaurant the restaurant that has the working hours info.
   */
  private setScheduleToShow(restaurant: Restaurant): void {
    const today = new Date();
    const todayWeekDay = today.getDay();
    for (const workingHour of restaurant.working_hours) {
      if (workingHour.weekDay === todayWeekDay) {
        this.todayWorkingHour = workingHour;
      }
    }
  }

  /**
   * Function that determinates if the menu option hast to show the dot indicating that a new reservation has arrived.
   * @param route The route of the section corresponding to the menu option
   * @returns Boolean that indicates if the dot should be showed.
   */
  public hasMenuOptionToShowDot(route: string): boolean {
    return (route === 'new-requests' &&
      this.hasNewRequest &&
      !this.isThisTheNewReservationsCRUD) ||
      (route === 'in-service' &&
        this.hasNewInServiceReservations &&
        !this.isThisTheInServiceReservationsCRUD)
      ? true
      : false;
  }

  /**
   * Tells if the actual section is the New Reservations one.
   */
  private get isThisTheNewReservationsCRUD(): boolean {
    return this.router.url.endsWith('new-requests');
  }

  /**
   * Tells if the actual section is the In Service one.
   */
  private get isThisTheInServiceReservationsCRUD(): boolean {
    return this.router.url.endsWith('in-service');
  }
}
