import { Howl } from 'howler';

export const NotificationSound = new Howl({
  src: [
    '/assets/sounds/eventually-590.mp3',
    '/assets/sounds/eventually-590.ogg',
    '/assets/sounds/eventually-590.m4r',
  ],
});

/**
 * Play sound for notification
 * @param loop Determine if sound is a loop
 * @param type Sound type
 */
export const PlaySoundNotification = ( ): void => {
  StopSoundNotification();
  NotificationSound.loop(true);
  NotificationSound.play();
  NotificationSound.volume(1);
};

/**
 * Stop sound for notification when sound is a loop
 * @param type Sound type
 */
export const StopSoundNotification = (): void => {
  NotificationSound.stop();
};
