import { Reservation, ResponseReservation } from '@models';
import { Injectable } from '@angular/core';
import * as io from 'socket.io-client';
import { environment } from '@environments/environment';
import * as firebase from 'firebase/app';
import { MatSnackBar } from '@angular/material/snack-bar';
import { BehaviorSubject } from 'rxjs';

@Injectable({
  providedIn: 'root',
})
export class NewRequestAlertService {
  public socket: any;

  private token: string;

  public hasNewRequest: BehaviorSubject<boolean> = new BehaviorSubject(null);
  public currentNewRequestStatus = this.hasNewRequest.asObservable();

  public hasNewInServiceReservation: BehaviorSubject<boolean> = new BehaviorSubject(
    null
  );
  public currentHasNewInServiceReservation = this.hasNewInServiceReservation.asObservable();

  public newPendingReservation: BehaviorSubject<Reservation> = new BehaviorSubject(
    null
  );
  public currentNewPendingReservation = this.newPendingReservation.asObservable();

  public newCheckoutReservation: BehaviorSubject<Reservation> = new BehaviorSubject(
    null
  );
  public currentNewCheckoutReservation = this.newCheckoutReservation.asObservable();

  public newInServiceReservation: BehaviorSubject<boolean> = new BehaviorSubject(
    null
  );
  public currentNewInServiceReservation = this.newInServiceReservation.asObservable();

  public newCancelledReservation: BehaviorSubject<number> = new BehaviorSubject(
    null
  );
  public currentNewCancelledReservation = this.newInServiceReservation.asObservable();

  constructor(private dialog: MatSnackBar) {}

  /**
   * Connects websocket to restaurant room and listens for new space requests and order requests
   * @param restaurantId string containing the restaurant ID to be used as room identifier
   */
  public connect(restaurantId: string): void {
    /**
     * Gets current user token to later be used to connect websocket
     */
    firebase
      .auth()
      .currentUser.getIdToken()
      .then((idtoken) => {
        this.token = idtoken;
        /**
         * Creates Socket IO object using the propert environment URL
         */
        this.socket = io.connect(environment.baseUrl, {
          rejectUnauthorized: false,
        });

        this.socket.emit(
          'get',
          {
            url: '/sockets',
            params: {
              token: this.token,
              restaurant: restaurantId,
            },
          },
          () => {}
        );

        this.socket.on('start_reservation', (data: ResponseReservation) => {
          this.onNewRequestHandle(data);
        });

        this.socket.on(
          'send_reservation_orders',
          (data: ResponseReservation) => {
            this.onCheckoutRequestHandle(data);
          }
        );

        this.socket.on('reservations_in_service', () => {
          this.onInServiceRequestHandle(true);
        });

        this.socket.on(
          'cancel_reservation',
          (data: { reservation: { id: number } }) => {
            this.onCancelledRequestHandle(data.reservation.id);
          }
        );
      })
      .catch((error) => {
        this.showError(error);
      });
  }

  /**
   * Takes received reservation with 'pending' status and saves it in the service for component comsumption
   * @param data ResponseReservation object containing all the reservation information
   */
  public onNewRequestHandle(data: ResponseReservation): void {
    this.hasNewRequest.next(true);
    const newRequest = new Reservation(data.reservation);
    newRequest.is_new = true;
    this.newPendingReservation.next(newRequest);
  }

  /**
   * Takes received reservation with 'checkout' status and saves it in the service for component comsumption
   * @param data ResponseReservation object containing all the reservation information
   */
  public onCheckoutRequestHandle(data: ResponseReservation): void {
    this.hasNewRequest.next(true);
    const newRequest = new Reservation(data.reservation);
    newRequest.is_new = true;
    this.newCheckoutReservation.next(newRequest);
  }

  /**
   * When there are new reservations with 'inService' status we tells so to the listeners.
   * @param data ResponseReservation object containing all the reservation information
   */
  public onInServiceRequestHandle(areNewInServiceReservations: boolean): void {
    this.hasNewInServiceReservation.next(true);
    this.newInServiceReservation.next(areNewInServiceReservations);
  }

  /**
   * Takes received reservation and saves it in the service for component comsumption
   * @param data ResponseReservation object containing all the reservation information
   */
  public onCancelledRequestHandle(reservationId: number): void {
    this.newCancelledReservation.next(reservationId);
  }

  /**
   * Shows a snackbar with an error message.
   * @param errorCode Message to show at the snackbar.
   */
  private showError(error: string): void {
    this.dialog.open(error, 'Entendido', {
      duration: 15000,
    });
  }
}
