import { NgModule } from '@angular/core';
import { RouterModule, Routes } from '@angular/router';
import { StaffLayoutComponent } from './staff-layout.component';
import { FranchiseAndRestaurantValidityGuard, ClaimsGuard } from '@guards';

const routes: Routes = [
  {
    path: '',
    pathMatch: 'full',
    redirectTo: 'new-requests',
  },
  {
    path: '',
    component: StaffLayoutComponent,
    canActivate: [ClaimsGuard, FranchiseAndRestaurantValidityGuard],
    children: [
      {
        path: 'new-requests',
        loadChildren: () =>
          import('./new-requests/new-requests.module').then(
            (m) => m.NewRequestsModule
          ),
      },
      {
        path: 'reservations',
        loadChildren: () =>
          import('./reservations/reservations.module').then(
            (m) => m.ReservationsModule
          ),
      },
      {
        path: 'in-service',
        loadChildren: () =>
          import('./in-service/in-service.module').then(
            (m) => m.InServiceModule
          ),
      },
      {
        path: 'closed',
        loadChildren: () =>
          import('./closed/closed.module').then((m) => m.ClosedModule),
      },
      {
        path: 'menu',
        loadChildren: () =>
          import('./staff-menu/staff-menu.module').then(
            (m) => m.StaffMenuModule
          ),
      },
    ],
  },
];

@NgModule({
  imports: [RouterModule.forChild(routes)],
  exports: [RouterModule],
})
export class StaffRoutingModule {}
