import { Observable } from 'rxjs';
import {
  Component,
  Input,
  OnChanges,
  OnInit,
  ViewChild,
  ElementRef,
  ChangeDetectorRef,
} from '@angular/core';
import { PlaySoundNotification } from '@utils/sounds.utils';
@Component({
  selector: 'app-notification-sound-enabler',
  templateUrl: './notification-sound-enabler.component.html',
  styleUrls: ['./notification-sound-enabler.component.scss'],
})
export class NotificationSoundEnablerComponent implements OnInit, OnChanges {
  @ViewChild('ghostButtonThatActivatesSound') ghostButtonThatActivatesSound: ElementRef;
  @Input() notificationEvents: Observable<unknown>;

  constructor(private readonly cdr: ChangeDetectorRef) {}

  ngOnInit(): void {
    this.notificationEvents.subscribe(() => {
      this.ghostButtonThatActivatesSound.nativeElement.click();
    });
  }

  ngOnChanges(): void {
    this.cdr.detectChanges();
  }

  public clickGhostButtonToPlaySound(): void {
    PlaySoundNotification();
  }
}
